body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin: 10px;
}

nav {
  border-bottom: 4px solid #475d3e;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: #000;
  z-index: 2;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background:#475d3e;
  color: yellow;
}
nav a:focus, a:visited{
  background : #475d3e;



}
