.react-sampls li a { text-decoration: none; line-height: 1.8;}
.react-sampls li a:hover {font-weight: bold;}
.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  
}
.widecard:hover
{
  color: #fff;
  background-color: #4d4d4d;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.widecard a{text-decoration: none; color:green; font-size: 20px; margin: 10px; }
.widecard a:hover{ color:red}
.social
{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover
{
  color: #ffffff;
}
