/* ===================================================== */

.footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align:center;
}
.footer ul p{ margin-left:100px;}
.footer li {
  display: inline-block;
  padding-top: 6px;
  padding-bottom: 16px;
}
.footer li:last-of-type{margin-right: 100px;}
.footer a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

.footer a:hover {
  background: black;
  color: white;
}

/* -------------------------------------------- */

#page-body {
  max-width: 700px;
  min-height: 600px;
  margin: auto;
  padding-top: 64px;
  padding-bottom : 64px;
}
.article{display: flex; }
.article .col1{
    flex:68%;
    padding-right: 10px;
}
.article .col2{
  padding-left:30px;
 
}
.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

.yoga-item-Container{
 display: flex; 
 height: 250px;
 padding-top: 15px;
}
.yoga-item-Container .col1{
  flex: 50%;
}
.yoga-item-Container .col2{
  padding-left: 50px;
  margin-top: -20px;

}
.yoga-item-Container .col2 p{
  margin-bottom: 10px;
}
.yoga-item-Container .col2 span{
  margin-bottom: 40px;
}
.buttonslist{
  text-align:center;
}
#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.pagination {
  margin: 15px auto;
  margin-left: 200px;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination > .active > a{
  background-color: #47ccde ;
  border-color: #47ccde ;
  color: #fff;
}
.pagination > li > a{
  border: 1px solid #000 ;
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
}
.pagination > .active > a, .pagination > .active > span, .pagination > .active > a:hover, .pagination > .active > span:hover, .pagination > .active > a:focus, .pagination > .active > span:focus{
  background-color: #000 ;
  border-color: #000;
  outline: none ;
}
.pagination > li > a, .pagination > li > span{
  color: #000
}
.pagination > li:first-child > a, .pagination > li:first-child > span, .pagination > li:last-child > a, .pagination > li:last-child > span{
  border-radius: unset
}